import React from 'react';
import Link from 'next/link';

export const Header = () => {
    return (
        <nav>
            <Link href="/">
                <a title="Last Post">Last Post</a>
            </Link>
            <Link href="/posts">
                <a title="List of posts">List of posts</a>
            </Link>
            <Link href="/new">
                <a title="New post">New post</a>
            </Link>
        </nav>
    );
}